	import '../index.scss'
	import uPopup from '../../../components/basic-component/u-popup/u-popup.vue';
	let interval = null

	export default {
		data() {
			return {
				time:60,
				buildvalue: [],
				infovalue: [],
				visible: true,
				indicatorStyle: `height: 50px;`,
				buildInfo:[],
				popupshow:false,
				infoshow:false,
				form:{
					mobile:'',
					code:'',
					build_id:'',
					unit:'',
					floornum:'',
					number:'',
					name:'',
					note:''
				},
				unitFloorInfo:{
					unit:[], floornum:[]
				},
				buildItem:{},
				unitItem:{},
				floorItem:{},
				numberItem:{},
				checkType:''
			}
		},
		components: {
			uPopup
		},
		computed: {

		},
		created() { 
			this.getBuilding()
		},
		onReachBottom() {

		},
		destroyed(){
			console.log('组件被销毁');
			clearInterval(interval)
		},
		onHide(){
			clearInterval(interval)
		},
		methods: {
			async sendCode(){
				if(this.form.mobile == ''){
          return uni.showToast({title: '请先输入手机号', icon: 'none'});
				}
				try{
          let info =  await this.$request({
              url: this.$api.myrecharge.sendCode,
							method: 'post',
              data: {mobile: this.form.mobile}
          })
          let { code, data, msg } = info;
          if (code === 1) {
						this.time--
						interval = setInterval(() => {
							this.time--
							if(this.time === 0) {
								clearInterval(interval)
								this.time = 60
							}
						},1000)
          } else {
              uni.showToast({title: msg, icon: 'none'});
          }
        }catch(e){
						uni.showToast({title: '验证码发送失败', icon: 'none'});
            this.$hideLoading();
        }
			},
      async getBuilding (){
        try{
          let info = await this.$request({
              url: this.$api.myrecharge.getBuilding,
              data: {}
          })
          let { code, data, msg } = info;
          if (code === 0) {
            this.buildInfo = data.list
						this.$forceUpdate();
          } else {
              // uni.showToast({title: msg, icon: 'none'});
          }
        }catch(e){}
      },
      async getUnitFloor (_id){
        try{
          let info = await this.$request({
              url: this.$api.myrecharge.getUnitFloor,
              data: {build_id: _id}
          })
          let { code, data, msg } = info;
          if (code === 0) {
            this.unitFloorInfo = data
						this.$forceUpdate();
          } else {
              // uni.showToast({title: msg, icon: 'none'});
          }
        }catch(e){}
      },

			bindPickerChange(e) {
					const val = e.detail.value
					this.buildItem = this.buildInfo[val]
					this.form.build_name = this.buildItem.name
					this.form.build_id = this.buildItem.id
					this.getUnitFloor(this.buildInfo[val].id)
					this.inputChange()
			},
			bindInfoChange(e, type){
				const val = e.detail.value
				this.form[type] = this.unitFloorInfo[type][val]
				this.inputChange()
			},
			check(_type){
				if(!_type){
				this.popupshow = true
				}else{
					this.infovalue = []
					this.infoshow = true
					this.checkType = _type
				}
			},
			inputChange(){
				this.$emit('getForm', this.form)
			}
		}
	}
